export default {
  props: {
    model: {
      type: Object
    },
    rules: {
      type: Object
    }
  },
  methods: {
    // 给用户调用的
    validator: function validator(cb) {
      var errorObj = {}; // 获取检验是否成功

      var flag = this.$children.every(function (child) {
        return child.validateStatus !== 'error';
      }); // 获取formItem校验信息

      var errorData = this.$children.map(function (children) {
        return children.valiStatus;
      }).flat();

      for (var i = 0; i < errorData.length; i++) {
        var data = errorData[i];
        var name = data['field'];
        errorObj[name] = data;
      }

      cb(flag, errorObj);
    }
  },
  provide: function provide() {
    return {
      form: this
    };
  }
};